$color: #073b4c;
$color: #06d6a0;

.header {
  height: 200px;
  background-color: #ef476f;
}

.footer {
  height: 200px;
  background-color: #118ab2;
}

.box {
  width: 100px;
  height: 100px;
  background-color: #ffd166;
  display: flex;
  transition: all 0.5s;
  &:hover {
    width: 200px;
    background-color: $color;
  }
}
